async function GetForecastCategorylist(instance,modulename, filtername) {
    var vm = instance;
    let params = {};
    params["moduleName"] = modulename; 
    params["type"] = filtername
  
   await StageDataService.GetLeftSearchStatus(params)
        .then(function (response) {
        var json = response.data;            
            if (json != null) {        
                vm.leftSearchFields[1].listOptions = json.DATA;
                return vm.Forecastcategorylist = json.DATA;               
            }
        });
}

export default { buttons, leftSearchFields ,listActions,GetForecastCategorylist};